.noResults-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 60px;
}

.noResults-image {
  align-self: center;
  height: auto;
  width: 350px;
}

@media (max-width: 768px) {
  .noResults-container {
    margin-top: 30px;
  }
}

@media (max-width: 480px) {
  .noResults-container {
    margin-top: 15px;
  }

  .noResults-image {
    width: 100%;
  }
}
